@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  .shadow-custom {
    --tw-shadow: 0 25px 50px -12px rgba(219, 39, 119, 0.75),
      0 10px 10px -10px rgba(204, 81, 167, 0.6);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000),
      var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
  outline: none;
}

body {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow-x: hidden;
  user-select: none;
  overflow: scroll;
  background-color: rgb(234, 234, 234);
  overflow-x: hidden;
}
.main-black-color {
  background-color: #3e8a91;
}

html {
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100%));
}

::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #1e293b;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

.customborder {
  border-right-width: 2px;
  border-left-width: 2px;
  border-left-color: rgb(225, 225, 225);
  border-right-color: rgb(225, 225, 225);
}

* {
  scrollbar-color: #1e293b transparent;
}

* {
  scrollbar-face-color: #1e293b;
  scrollbar-track-color: transparent;
}

.loader {
  width: 60px;
  height: 60px;
}

.loader::before {
  content: "";
  box-sizing: border-box;
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border-top: 2px solid rgb(136, 0, 0);
  border-right: 2px solid rgb(122, 0, 0);
  animation: spinner8217 0.8s linear infinite;
}

@keyframes spinner8217 {
  to {
    transform: rotate(360deg);
  }
}
@keyframes rainbow {
  0% {
    color: #ff0000;
  } /* Red */
  16% {
    color: #ff7f00;
  } /* Orange */
  33% {
    color: #ffff00;
  } /* Yellow */
  50% {
    color: #00ff00;
  } /* Green */
  66% {
    color: #0000ff;
  } /* Blue */
  83% {
    color: #4b0082;
  } /* Indigo */
  100% {
    color: #9400d3;
  } /* Violet */
}

.text-rainbow {
  animation: rainbow 5s infinite;
}
